<template>
    <section class="navbar-section">
        <span class="dropdown dropdown-right" v-if="isLoaded">
            <button class="link toggle-dropdown" @click="dropdownActive = !dropdownActive">
                {{ organization.OrganizationName }}
                <i class="icon icon-caret-down" v-if="userTheme"></i>
            </button>
            <ul id="accountDropdown" class="menu" :class="{ 'drop-active': dropdownActive }">
                <li class="menu-item">
                    <div class="tile tile-centered no-wrap">
                        <div class="tile-icon">
                            <img
                                v-if="userTheme && userTheme.CompanyLogoLight"
                                class="avatar"
                                :src="userTheme.CompanyLogoLight"
                                :alt="username + ' Avatar'"
                            />
                            <i class="avatar icon icon-user" v-else></i>
                        </div>
                        <div class="tile-content">{{ username }}</div>
                    </div>
                </li>
                <li class="divider"></li>
                <slot name="menu-item"></slot>
                <li class="menu-item">
                    <a href="#" @click.prevent @mousedown="accountClick()">My Account</a>
                </li>
                <li class="menu-item">
                    <a href="#" @click.prevent @mousedown="logoutClick()">Logout</a>
                </li>
            </ul>
        </span>
        <span class="loading d-block px-3" v-else></span>
    </section>
</template>

<script>
export default {
    name: "BrandedNavbarMenu",
    data() {
        return {
            isLoaded: false,
            userTheme: this.theme,
            dropdownActive: false
        };
    },
    methods: {
        accountClick: function() {
            window.location.href = "https://account.ridestyler.com/";
        },
        logoutClick: function() {
            ridestyler.auth.signOut().then(() => {
                window.location.reload();
            });
        }
    },
    created() {
        let processes = [],
        theme = null;

        if (!this.userTheme) {
            theme = new Promise((resolve, reject) => {
                ridestyler.ajax.send({
                    action: "Client/GetTheme",
                    callback: response => {
                        this.userTheme = response.Theme;
                        response.Success ? resolve(response) : reject(response);
                    }
                });
            });
            processes.push(theme);
        } else {
            this.isLoaded = true;
            return;
        }

        Promise.all(processes)
        .then(response => {
            if (response) this.isLoaded = true;
        })
        .catch(response => {
            if (response) this.isLoaded = false;
        });

        document.body.addEventListener('click', (e) => {
            if(!e.target.classList.contains('toggle-dropdown')) this.dropdownActive = false;
        });
    },
    computed: {
        username() {
            let username = this.organization.OrganizationName;
            if (this.user) username = this.user.UserName;
            return username;
        }
    }
};
</script>

<style lang="scss">
.dropdown .menu.drop-active {
  display: inline-block;
}
.no-wrap {
  white-space: nowrap;
}
.avatar.icon:before {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.icon {
  font-family: "rsui-icons" !important;
}
</style>